const homeRoute =           {path: "/", generate: () => ""} //
const modificationsRoute =  {path: "/:vin", generate: (vin) => `/${vin}`} //
const modificationRoute =   {path: "/:vin/:car", generate: (hash) => `/${hash}`} //
const groupRoute =          {path: "/:vin/:car/:group", generate: (group) => `/${group}`} //
const partRoute =           {path: "/:vin/:car/:group/:part", generate: (part) => `/${part}`} //

const search =  {path: "/search/:make_model", generate: (make_model) => `/search/${make_model}`} // 
const searchStep1 =  {path: "/search/:make_model/:step1", generate: (step1) => `${step1}`} //  1 
const searchStep2 =  {path: "/search/:make_model/:step1/:step2", generate: (step2) => `${step2}`} //  1 

export { homeRoute, modificationRoute, modificationsRoute, groupRoute, partRoute, search, searchStep1, searchStep2 }